"use client";

import Link from 'next/link';
import { motion } from 'framer-motion';

export default function CTA() {
  return (
    <section id="contact" className="section bg-background">
      <div className="container">
        <motion.div 
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.3 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          className="stylish-box text-center mb-16"
          style={{ 
            position: 'relative',
            zIndex: 1 
          }}
        >
          <h2 className="text-3xl md:text-4xl font-bold mb-4">WORK WITH US TODAY</h2>
          <p className="text-2xl font-bold mb-4">
            Ready to Level Up?
          </p>
          <p className="text-muted-100 dark:text-muted-200 max-w-xl mx-auto">
            Step into the future with FNX Labs. Let's build a smarter, more automated organization - reach out today!
          </p>
        </motion.div>
        
        {/* Contact form */}
        <motion.div 
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.2 }}
          transition={{ duration: 0.8, ease: "easeOut", delay: 0.2 }}
          className="max-w-3xl mx-auto"
          style={{ 
            position: 'relative',
            zIndex: 1 
          }}
        >
          <motion.div 
            initial={{ opacity: 0, scale: 0.98 }}
            whileInView={{ opacity: 1, scale: 1 }}
            viewport={{ once: true }}
            transition={{ duration: 0.8, ease: "easeOut" }}
            className="bg-accent p-8 rounded-xl border border-muted-200"
            style={{ 
              position: 'relative',
              zIndex: 1 
            }}
          >
            <h3 className="text-2xl font-bold mb-6 text-center">Get in Touch</h3>
            
            <form className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <motion.div
                  initial={{ opacity: 0, x: -20 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.6, ease: "easeOut" }}
                  style={{ position: 'relative', zIndex: 1 }}
                >
                  <label htmlFor="name" className="block mb-2 text-sm font-medium">
                    Name
                  </label>
                  <input
                    type="text"
                    id="name"
                    className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                    placeholder="Your name"
                    required
                  />
                </motion.div>
                <motion.div
                  initial={{ opacity: 0, x: 20 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.6, ease: "easeOut", delay: 0.1 }}
                  style={{ position: 'relative', zIndex: 1 }}
                >
                  <label htmlFor="email" className="block mb-2 text-sm font-medium">
                    Email
                  </label>
                  <input
                    type="email"
                    id="email"
                    className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                    placeholder="Your email"
                    required
                  />
                </motion.div>
              </div>
              
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.6, ease: "easeOut", delay: 0.2 }}
                style={{ position: 'relative', zIndex: 1 }}
              >
                <label htmlFor="phone" className="block mb-2 text-sm font-medium">
                  Phone
                </label>
                <input
                  type="tel"
                  id="phone"
                  className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                  placeholder="Your phone number"
                  required
                />
              </motion.div>
              
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.6, ease: "easeOut", delay: 0.3 }}
                style={{ position: 'relative', zIndex: 1 }}
              >
                <label htmlFor="message" className="block mb-2 text-sm font-medium">
                  How can we help?
                </label>
                <textarea
                  id="message"
                  rows={5}
                  className="w-full p-3 rounded-lg border border-muted-200 bg-background focus:outline-none focus:ring-2 focus:ring-primary/50"
                  placeholder="Tell us about your business needs"
                  required
                />
              </motion.div>
              
              <motion.button
                initial={{ opacity: 0, y: 20 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.6, ease: "easeOut", delay: 0.4 }}
                whileHover={{ scale: 1.03 }}
                whileTap={{ scale: 0.98 }}
                type="submit"
                className="w-full btn-primary"
                style={{ position: 'relative', zIndex: 1 }}
              >
                Let's Talk
              </motion.button>
            </form>
          </motion.div>
        </motion.div>
      </div>
    </section>
  );
} 